* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body,
html {
    width: 100%;
    height: 100%;

}

.main {
    position: relative;

}

.box-shadow {
    box-shadow: 0px 4px 20px 4px rgba(0, 0, 0, 0.54);

}

.img-banner {
    position: relative;
    object-fit: cover;
    height: 100vh;
}

.img-banner img {
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.banner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.566);
}

nav {
    width: 100%;
    top: 0;
    position: absolute;
    padding: 0 2rem;
    background-color: rgba(0, 0, 0, 0.497);
    box-shadow: 0px 4px 20px 4px rgba(0, 0, 0, 0.54);
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 8.5rem;
}

.logo img {
    width: 190px;
}

.menu {
    display: inline-flex;
    gap: 1.3rem;
}

.s {
    display: flex;
    align-items: center;
    border: 1px solid #6564649e;
    border-radius: 4px;
    padding: 0 0.5rem;
}

.s>i {
    color: #fff;
    position: absolute;

}

.menu>.s>select {
    outline: #fff;
    background-color: rgba(39, 38, 38, 0.147);
    color: #fff;
    min-width: auto;
    height: 32px;
    letter-spacing: 0.5px;
    font-size: 0.9rem;
}

.menu :nth-child(2) {
    padding: 0.5rem 0.9rem;
    background-color: red;
    border: none;
    color: #fff;
    font-weight: 400;
    font-size: 14px;
    border-radius: 4px;
}

.menu a {
    color: #fff;
    text-decoration: none;
}

.heading-sec {
    text-align: center;
    top: 250px;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    color: #fff;
}

.headings {
    margin: 1.5rem 12rem;
}

.heading-sec h2 {
    font-size: 3rem;
    font-weight: 800;
    font-family: netflix2;
    letter-spacing: 0.1px;

}

.heading-sec :nth-child(2) {
    margin: 1rem 0 0 0;
    font-weight: 100;
    font-family: netflix1;
    font-size: 1.5rem;
}

.btn-sec {
    margin: 1rem 12rem;

}

.btn-sec p {
    font-weight: 100;
    font-family: netflix1;
    font-size: 1.25rem;

}

.heading-sec input {
    width: 25%;
    padding: 0 1rem;
    border-radius: 4px;
    color: #fff;
    height: 59px;
    background-color: #2a2a2a67;
    border: 1px solid rgba(255, 255, 255, 0.304);
}

.heading-sec input::placeholder {

    color: #c5c4c4;
    font-size: 1rem;


}

.heading-sec button {
    height: 58px;
    width: 14%;
    font-size: 1.5rem;
    border: none;
    border-radius: 4px;
    background-color: red;
    color: #fff;
    font-weight: 600;
}

.page2 {
    height: 600px;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    margin: -1px 0 0 0;
}

hr {
    border-top: 8px solid rgba(7, 7, 7, 0.899);

}

.content {
    display: flex;
    justify-content: center;
    color: #fff;
    height: 600px;
    width: 78%;
    align-items: center;
}

.text h1 {
    font-size: 3rem;
    font-weight: 900;
    font-family: netflix3;

}

.text p {
    margin: 1rem 0 0 0;
    font-size: 100;
    font-size: 1.4rem;
    line-height: 1.8rem;
    width: 88%;
    font-family: netflix1;
}

.tv {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
}

.tv img {
    z-index: 2;
    width: 100%;
}

.tv video {
    position: absolute;
    width: 77%;
    object-fit: contain;
    z-index: 1;
}


.page3 {
    height: 600px;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    margin: -1px 0 0 0;
}

.mobile {
    width: 68%;
    position: relative;
}

.mobile img {

    width: 100%;
}

.download {
    display: flex;
    position: absolute;
    align-items: center;
    padding: 0.5rem 0.75rem;
    border-radius: 12px;
    border: 2px solid rgba(128, 128, 128, 0.7);
    box-shadow: 0 0 2em 0 rgb(0, 0, 0);
    background-color: #000;
    width: 53%;
    left: 24%;
    bottom: 8%;
}

.mobile .stranger {
    width: 20%;
    margin-right: 1rem;
}

.download-text {
    width: 100%;
    font-family: netflix1;
}

.download-text :nth-child(1) {
    font-weight: 600;
}

.download-text :nth-child(2) {
    color: #0071EB;
}

.mobile .gif {
    width: 12%;

}

.txt h1 {
    font-size: 3rem;
    font-weight: 900;
    font-family: netflix3;

}

.txt p {
    margin: 1rem 0 0 0;
    font-size: 400;
    font-size: 1.5rem;
    line-height: 1.8rem;
    letter-spacing: 0.5px;
    width: 80%;
    font-family: netflix1;
}

.page4 {
    height: 600px;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    margin: -1px 0 0 0;
}




.pc {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    width: 60%;
}

.pc img {
    z-index: 3;
    width: 100%;
}

.pc video {
    position: absolute;
    width: 62%;
    top: 11%;
    z-index: 1;
}

.page5 {
    height: 600px;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    margin: -1px 0 0 0;
}

.sec {
    margin: 0rem 0.5rem;

}

.m {
    width: 99%;

}

.m img {
    width: 100%;
}

.page5 h1 {
    font-size: 3rem;
    font-weight: 900;
    font-family: netflix3;
}

.page5 p {
    margin: 1rem 0 0 0;
    font-size: 400;
    font-size: 1.5rem;
    line-height: 1.8rem;
    letter-spacing: 0.5px;
    width: 95%;
    font-family: netflix1;
}


.page6 {
    height: 135vh;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    margin: -1px 0 0 0;
}

.faq {
    width: 78%;
    color: #fff;
    margin: 4rem 0;
}

.faq h1 {
    font-size: 3rem;
    font-weight: 800;
    font-family: netflix3;
    text-align: center;
}

.more-sec {
    display: flex;
    margin: 0.6rem 0;
    background-color: #2D2D2D;
    align-items: center;
    padding: 0 1.5rem;
    justify-content: space-between;
    height: 88px;
    font-family: netflix1;
    font-weight: 400;
    font-size: 1.5rem;
}

.p-sec {
    text-align: center;
    margin: 3rem 0 0 0;

}

.p-sec p {
    margin-bottom: 1rem;
    font-size: 1.25rem;
    font-family: netflix1;
}

.p-sec input {
    width: 30%;
    padding: 0 1rem;
    border-radius: 4px;
    color: #fff;
    height: 56px;
    background-color: #2a2a2a67;
    border: 1px solid rgba(255, 255, 255, 0.304);
}

.p-sec input::placeholder {

    color: #c5c4c4;
    font-size: 1rem;


}

.bt {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
}

.p-sec button {
    height: 56px;
    width: 18%;
    font-size: 1.5rem;
    border: none;
    border-radius: 4px;
    background-color: red;
    color: #fff;
    font-weight: 600;
}

footer {
    height: 400px;
    background-color: #000;
    margin: -1px 0 0 0;
    display: flex;
    justify-content: center;
}

.menu-footer {
    width: 78%;
    font-family: netflix1;

}

footer p {
    color: #acacac;
    margin: 5rem 0 0 0;
    padding: 0 0.5rem;

}

.end {
    color: #acacac;
    margin: 1rem 0 0 0;

}

.menu-footer a {
    color: #acacac;
}

.menu-footer ul {
    display: flex;
    color: #fff;
    margin: 1rem 0 0 0;
    list-style: none;
}

.menu-footer ul li {
    width: 50%;
    font-size: 14px;
    padding: 0 0.5rem;
    text-decoration: underline;
    color: #a6a6a6;

}

.last {
    width: 75%;
}

.ss {
    margin: 1rem 0 0 0.5rem;
    display: inline-flex;
    align-items: center;
    border: 1px solid #6564649e;
    border-radius: 4px;
    padding: 0 0.5rem;
}

.ss i {
    color: #fff;
    padding: 0 0.5rem;

}

.ss>select {
    border: none;
    background-color: rgba(39, 38, 38, 0.147);
    color: #fff;
    min-width: auto;
    height: 32px;
    letter-spacing: 0.5px;
    font-size: 0.9rem;
}